import './App.css';
import {useState, useEffect, useRef} from 'react'
import { Stage, Layer, Circle, Text, Line, Group, Rect, Arrow} from 'react-konva';
import { CompactPicker} from 'react-color';
import { v4 as uuid } from 'uuid';
import useImage from 'use-image';

function Node({json, nodeData, mode, updateNode, showMenu, setselectedEle, setmousePos}){ //nodeData
  const textRef = useRef(null)
  const [image] = useImage(nodeData.image)
  const [rectbox, setrectbox] = useState({height: 0, width: 0})

  function handleDragNode(e, id){
    updateNode(id, 'coords', [e.target.x(), e.target.y()])
  }

  function handleLabelChange(id){
    let label
    const existingLabels = json.nodes.map(node => node.id);

    while (true) {
      label = prompt(existingLabels.includes(label) ? 'Label already exists! Please enter a unique label.' : 'Enter label for the new node:');
      if (!label) return; // Exit function if label is empty

      if (!existingLabels.includes(label)) {
        break; // Exit loop if label is unique
      }
    }

    updateNode(id, 'label', label)
  }

  useEffect(() => { //get text width height
    //const onelineWidth = textRef.current.measureSize(nodeData.id).width
    setrectbox(
      {
        height: textRef.current.height(),
        width: textRef.current.width()
      }
      //{height: textRef.current.height(), width: textRef.current.width()}
    )
  }, [nodeData.id])


  return(
    <Group //do we even need group??????? fix
        onDblClick={() => handleLabelChange(nodeData.id)}
        draggable = {mode === 'circle' ? true : false} //if line mode, not draggable
        onDragMove={e => handleDragNode(e, nodeData.id)}
        x = {nodeData.x} //this group is relative to the coords of the circle because draggable is weird for coordinates in react ugh
        y = {nodeData.y}
        onContextMenu={(e) => {
          e.evt.preventDefault();
          showMenu(true)
          setselectedEle(nodeData)
          setmousePos({x: e.evt.clientX, y:e.evt.clientY})
        }}
      >
        {image ? //load image
        <>
          <Circle //so this coords circle is 0,0 , 
          //but is relative the to groups positon, changin the coords will be based on where the group is
          radius = {nodeData.radius} 
          id = {nodeData.id}
          fill = {'white'}
          stroke = {nodeData.color}
          strokeWidth={1}
          />
          <Circle //so this coords circle is 0,0 , 
          //but is relative the to groups positon, changin the coords will be based on where the group is
          radius = {nodeData.radius} 
          id = {nodeData.id}
          fillPatternImage={image}
          fillPatternOffset={{x: image.width/2, y: image.height/2}}
          fillPatternScale={{
            x: (nodeData.radius * 2)/(image.height < image.width ? image.height: image.width), //use whatever is less
            y: (nodeData.radius * 2)/(image.height < image.width ? image.height: image.width)}}
          fillPatternRepeat = {'no-repeat'}
          stroke = {nodeData.color}
          strokeWidth={1}
          />
        </>
        : null}
        <Text
          ref = {textRef}
          x = {-rectbox.width/2}
          y = {nodeData.radius}
          //width = {null} //this makes drag nodes to near eachother grab the text, make a uhhhh box around text or align width with box??? fix
          align='center' //okay so makeing a set width makes dragging a bit off, somehow calulate the width manually based on text length...
          text = {nodeData.id}
          fontSize = {20}
          fontFamily = 'Calibri'
          fill = {nodeData.color}
          //offset={{x: rectbox.width/2}}
        />
      </Group>
    )
}

function LineLinks({linkData, points, setselectedEle, showMenu, setmousePos}){
  return (
    <Group
      onContextMenu={(e) => {
        e.evt.preventDefault();
        setselectedEle(linkData)
        showMenu(true)
        setmousePos({x: e.evt.clientX, y:e.evt.clientY})
      }}
    >
      <Line
        points = {[
          points[0], 
          points[1], 
          points[2], 
          points[3]
        ]}
        source = {linkData.source}
        target = {linkData.target}
        stroke = {linkData.color}
        strokeWidth = {2}
        hitStrokeWidth={15}
        fill = {linkData.color}
      />
    </Group>
  )
}

function ArrowLinks({linkData, points, updateLinks, setselectedEle, showMenu, setmousePos}){
  const textRef = useRef(null)
  const [rectbox, setrectbox] = useState({height: 0, width: 0})

  //calculate gap between line end and node
  const cutOff = 50 + 25; //this is between endpoints
  const slope = (points[3] - points[1]) / (points[2] - points[0]);
  
  const angle = Math.atan(slope);
  const xOffset = Math.cos(angle) * cutOff;
  const yOffset = Math.sin(angle) * cutOff;

  function handleLabelChange(){
    let label //we don't need to find identical labels for links

    label = prompt('Enter a new label for the link:');
    if (!label) return; // Exit function if label is empty

    updateLinks('LabelUpdate', [linkData.source, linkData.target, label])
  }

  useEffect(() => { //get text width height
    setrectbox(
      {
        height: textRef.current.height(),
        width: textRef.current.width()
      }
    )
  }, [linkData.label])

  return(
    <>
      <Group
        onContextMenu={(e) => {
          e.evt.preventDefault();
          setselectedEle(linkData)
          showMenu(true)
          setmousePos({x: e.evt.clientX, y:e.evt.clientY})
        }}
      >
        <Arrow
          points = {[
            points[0] + (points[0] < points[2] ? xOffset : -xOffset), 
            points[1] + (points[0] < points[2] ? yOffset : -yOffset), 
            points[2] + (points[0] < points[2] ? -xOffset : xOffset), 
            points[3] + (points[0] < points[2] ? -yOffset : yOffset)
          ]}
          source = {linkData.source}
          target = {linkData.target}
          stroke = {linkData.color}
          strokeWidth = {2}
          fill = {linkData.color}
          pointerAtBeginning = {linkData.twoArrow}
          hitStrokeWidth={15}
        />
        <Rect
          x = {(points[2] - points[0])/2 + points[0] - rectbox.width/2}
          y = {(points[3] - points[1])/2 + points[1] - rectbox.height/2}
          width = {rectbox.width}
          height = {rectbox.height}
          fill = 'white'
          stroke= {linkData.color}
          strokeWidth={1}
        />
        <Text
          onDblClick={() => handleLabelChange()}
          ref = {textRef}
          x = {(points[2] - points[0])/2 + points[0] - rectbox.width/2}
          y = {(points[3] - points[1])/2 + points[1] - rectbox.height/2}
          text = {linkData.label}
          fontSize = {20}
          fontFamily = 'Calibri'
          fill = {linkData.color}
          padding={5}
        />
      </Group>
    </>
  )
}
///

function App(){
  const [json, setjson] = useState({nodes: [], links: []}) //nodes and links json
  const [line, setline] = useState(null) //the line we make if we are connect nodes, is null when we are actively not makeing a connection
  const [mode, setMode] = useState('circle')
  const [menuBool, showMenu] = useState(false)
  const [color, setColor] = useState('#000000')
  const [mousePos, setmousePos] = useState({x: 0, y: 0})
  const [selectedEle, setselectedEle] = useState(null)

  const [lineholding, setHolding] = useState(false) //holding mouse down for line connection
  const stageRef = useRef(null)

  useEffect(() => {
    const modeArray = ['circle', 'arrow', 'line']
    const handleKeyDown = (e) => {
      if (e.key === 'z') {

        const index = modeArray.findIndex(m => m === mode)

        // Toggle mode between circle and line
        setMode(modeArray[(index + 1) % modeArray.length]);
      }
    };
    // Add event listener for keydown
    document.addEventListener('keydown', handleKeyDown);

    // Cleanup event listener
    return () => {
      document.removeEventListener('keydown', handleKeyDown);
    };
  }, [mode]);

  /////
  function importFile(evt){
    const file = evt.target.files[0];
    const reader = new FileReader();

    reader.onload = function(event) {
        const contents = JSON.parse(event.target.result);
        // Process the file contents here
        const modnodes = contents.nodes.map(nodes => ({...nodes, uID: uuid()}))

        const modJson = {links: contents.links, nodes: modnodes}
        setjson(modJson)
    }
    reader.readAsText(file);
  }

  function convertToImage(){ //come back to this later, fix
    setselectedEle(null)
    const stage = stageRef.current.getStage();
    const oldSize = { width: stage.width(), height: stage.height(), pos: stage.position(), scale: stage.scaleX()};


    let minX = json.nodes[0].x
    let minY = json.nodes[0].y
    let maxX = json.nodes[0].x
    let maxY = json.nodes[0].y
    const gap = 50

    json.nodes.forEach(shape => { //sooooo it also cut off any long words of nodes ugh
      const shapeX = shape.x;
      const shapeY = shape.y;

      minX = Math.min(minX, shapeX - 50);
      minY = Math.min(minY, shapeY - 50);
      maxX = Math.max(maxX, shapeX + 50);
      maxY = Math.max(maxY, shapeY + 50);
    });

    const boundingBox = {
      x: minX - gap,
      y: minY - gap,
      width: maxX - minX + (gap * 2),
      height: maxY - minY + (gap * 2)
    };

    // Expand the canvas size to include all shapes
    stage.width(boundingBox.width);
    stage.height(boundingBox.height);
    stage.scale({x: 1, y: 1})
    stage.position({ x: -boundingBox.x, y: -boundingBox.y });

    const canvas = stage.toCanvas({pixelRatio: 2});
    const ctx = canvas.getContext('2d');

    ctx.globalCompositeOperation = 'destination-over';
    ctx.fillStyle = 'white';
    ctx.fillRect(0, 0, canvas.width, canvas.height);

    const image = canvas.toDataURL(); // This will give you a base64 encoded PNG image

    var download = function(href, name){ //download thing
      var link = document.createElement('a');
      link.download = name;
      link.style.opacity = "0";
      link.href = href;
      link.click();
      link.remove();
    }
    download(image, "image.png");

    // Restore the original stage size and position
    stage.width(oldSize.width);
    stage.height(oldSize.height);
    stage.scale({x: oldSize.scale, y: oldSize.scale})
    stage.position(oldSize.pos);
  }


  function convertToJSON(){
    //get rid of the uuid on export
    const modnodes = json.nodes.map(({uID, ...rest}) => rest)
    const modJson = {links: json.links, nodes: modnodes}
    const svgBlob = new Blob([JSON.stringify(modJson)], { type: 'application/json' });
    const url = URL.createObjectURL(svgBlob) //make it a url

    var link = document.createElement('a');
    link.download = 'network.json';
    link.style.opacity = "0";
    link.href = url;
    link.click();
    link.remove();
  }

  function findCoordfromID(id){
    const foundId = json.nodes.find((row) => (row.id === id)) //found matching id
    const {x, y} = foundId
    return {x, y}
  }

  function addNode(node){
    setjson(prev => ({...prev, nodes: [...prev.nodes, node]})) //add new node
  }

  function updateNode(nodeid, type, args){ //node id, what type of update, arguments (array if more than 1)
    const copynodes = [...json.nodes]
    const nodeIndex = copynodes.findIndex(node => node.id === nodeid) //find index
    if (nodeIndex === -1) return console.log(`Node with id ${nodeid} not found.`) //id not found but this shouldn't happen?

    if (type === 'coords'){
      // Update the x and y positions of the node
      copynodes[nodeIndex].x = args[0];
      copynodes[nodeIndex].y = args[1];
    }
    else if (type === 'label'){
      //update label (this also update links)
      const oldID = copynodes[nodeIndex].id 
      const newID = args
      copynodes[nodeIndex].id = args; //new label
      updateLinks('IDupdate', [oldID, newID])
    }
    else if (type === 'color'){
      //update color of node
      copynodes[nodeIndex].color = args
    }
    else if (type === 'delete'){
      const oldID = copynodes[nodeIndex].id 

      //delete node
      copynodes.splice(nodeIndex, 1)
      updateLinks('DeleteFromNodeUpdate', oldID)
    }
    else{
      return console.log(`No updated nodes due to unkown type of update.`)
    }
    setjson(prev => ({...prev, nodes: copynodes}))
  }

  function addLink(link){
    setjson(prev => ({...prev, links: [...prev.links, link]}))
  }

  function updateLinks(type, args){
    const copylinks = [...json.links]
    if (type === 'IDupdate'){ //args[0] is oldID, args[1] is newID, replace the ID
      const oldID = args[0]
      const newID = args[1]

      const copycopylinks = copylinks.map((link) =>{
        if (link.source === oldID && link.target === oldID) {
          // If they do, replace both with the replacementWord
          return {source: newID, target: newID};
        }
        else if (link.source === oldID) {
          // If it does, replace it with the replacementWord
          return { ...link, source: newID};
        }
        // Check if the target node matches the wordToReplace
        else if (link.target === oldID) {
          // If it does, replace it with the replacementWord
          return { ...link, target: newID};
        }

        return link; //no old id so just return
      })
      
      setjson(prev => ({...prev, links: copycopylinks}))
      return
    }
    else if (type === 'DeleteFromNodeUpdate'){
      const delID = args

      const copycopylinks = copylinks.filter((link) =>
        !(link.source === delID || link.target === delID) //if it containes the del id, do NOT return it/false
      )

      setjson(prev => ({...prev, links: copycopylinks}))
      return
    }
    else if (type === 'DeleteUpdate'){
      const source = args[0]
      const target = args[1]

      const LinkIndex = copylinks.findIndex(link => link.source === source && link.target === target) //find the link
      copylinks.splice(LinkIndex, 1)
    }
    else if (type === 'LabelUpdate'){
      const source = args[0]
      const target = args[1]
      const newLabel = args[2]

      const LinkIndex = copylinks.findIndex(link => link.source === source && link.target === target) //find the link
      copylinks[LinkIndex].label = newLabel
    }
    else if (type === 'ColorUpdate'){
      const source = args[0]
      const target = args[1]
      const color = args[2]

      const LinkIndex = copylinks.findIndex(link => link.source === source && link.target === target) //find the link
      copylinks[LinkIndex].color = color
    }
    else if (type === 'DirectionUpdate'){
      const source = args[0]
      const target = args[1]
      const arrowtype = args[2] //reverse or twoway

      const LinkIndex = copylinks.findIndex(link => link.source === source && link.target === target) //find the link

      if (arrowtype === 'reverse'){ //switch source and target
        copylinks[LinkIndex].source = target
        copylinks[LinkIndex].target = source
      }
      else{
        copylinks[LinkIndex].twoArrow = !(copylinks[LinkIndex].twoArrow) //flips it
      }
    }
    else{
      return console.log(`No updated nodes due to unkown type of update.`)
    }
    setjson(prev => ({...prev, links: copylinks}))
  }

  function LinkMenuOptions(){ //for menu with links
    if ('id' in selectedEle){ //node
      return null
    }
    else{ //link
      if (selectedEle.type === 'arrow'){ //arrows
        if (selectedEle.twoArrow){ //two arrow
          return (
            <button className='buttonMenu' onClick={() => 
              updateLinks("DirectionUpdate", [selectedEle.source, selectedEle.target, 'twoArrow'])
            }>Make One Way Arrow</button>
          )
        }
        else{ //one arrow
          return(
            <>
              <button className='buttonMenu' onClick={() => 
                updateLinks("DirectionUpdate", [selectedEle.source, selectedEle.target, 'twoArrow'])
              }>Make Two Way Arrow</button>
              <br/>
              <button className='buttonMenu' onClick={() =>
                updateLinks("DirectionUpdate", [selectedEle.source, selectedEle.target, 'reverse'])
              }>Switch Arrow Direction</button>
            </>
          )
        }
      }
      else{ //line
        return null
      }
    }
  }

  function explainMode(){
    if (mode === 'circle'){
      return (
      <>
        <p>Drag the node to move it</p>
      </>
    )
    }
    else if (mode === 'arrow'){
      return (
        <>
          <p>Drag the node to make an arrow</p>
          <p>Connect it to another node to make a network, type in a label when you connect to a node</p>
        </>
      )
    }
    else if (mode === 'line'){
      return (
        <>
          <p>Drag the node to make a line</p>
          <p>Connect it to another node to make a network</p>
        </>
      )
    }
    else{
      return <p>"???"</p>
    }
  }

  ///

  function handleDropImage(e){ //handling drop events (images)
    e.preventDefault() //skips the image opening on new tab
    const file = e.dataTransfer.files[0] //gets file dragged in (this specific path only, global dont have it...)
    if (!file.type.startsWith("image/")){
      alert("That is not an image file")
      return
    }

    const reader = new FileReader();

    reader.onload = () => {

      let label

      const existingLabels = json.nodes.map(node => node.id);

      while (true) {
        label = prompt(existingLabels.includes(label) ? 'Label already exists! Please enter a unique label.' : 'Enter label for the new node:');
        if (!label) return; // Exit function if label is empty

        if (!existingLabels.includes(label)) {
          break; // Exit loop if label is unique
        }
      }

      const {x, y} = stageRef.current.getStage().position() //get coords of panning canvas
      const scale = stageRef.current.getStage().scaleX();

      const ele = {
        type: 'circle', 
        x: (e.clientX - x) / scale, 
        y: (e.clientY - y) / scale, 
        radius: 50, image: reader.result, 
        uID: uuid(), //this is so we can differentiate nodes
        id: label, //label also acts as id and text
        color: color
      }
      addNode(ele) //add new node
    }
    reader.readAsDataURL(file); //readingg
  }

  function handleMouseDown(e){
    if(e.target.getClassName() === "Circle" && mode !== 'circle' && e.evt.which !== 3){ //click on circle, start a line
      //get the circle points
      const {id} = e.target.attrs

      const {x, y} = findCoordfromID(id)

      const ele = {
        startPoint: [x, y],
        endPoint: [x, y],
      }

      setline({...ele, source: id})
      setHolding(true)
    }
  }

  function handleMouseMove(e){
    if (mode !== 'circle' && lineholding === true){ //line or arrow, just not circle mode
      const {x, y} = stageRef.current.getStage().position() //get coords of panning canvas
      const scale = stageRef.current.getStage().scaleX();

      const copyline = {
        ...line,
        endPoint: [(e.evt.clientX - x) / scale, (e.evt.clientY - y) / scale]
      }
      setline(copyline)
    }
  }

  function handleMouseUp(e){
    if (mode !== 'circle' && lineholding === true){ //if let go of line
      if (e.target.getClassName() === "Circle" && mode === 'arrow'){ //let go on circle
        const {id} = e.target.attrs

        if (line.source === id){ //return as you shouldn't connect with yourself
          setline(null)
          setHolding(false)
          return
        }

        const linkExists = json.links.some(edge => ( //check if links exist (change later in case of one way direction not two way, or parallel lines) fix?
          (edge.source === line.source && edge.target === id) ||
          (edge.source === id && edge.target === line.source)
        ));

        if (linkExists) { //link exist to return
          setline(null)
          setHolding(false)
          return
        }

        let label
        label = prompt('Enter label for the new node:'); //its fine to have identical labels
        
        if (!label){
          setline(null)
          setHolding(false)
          return
        }

        const copyline = { //do this so line end point is at the middle of circle
          type: 'arrow',
          source: line.source,
          target: id,
          label: label,
          color: color,
          twoArrow: false //twoway arrow is false by default
        }
        addLink(copyline)
        setline(null)
        setHolding(false)
      }
      else if (e.target.getClassName() === "Circle" && mode === 'line'){
        const {id} = e.target.attrs

        if (line.source === id){ //return as you shouldn't connect with yourself
          setline(null)
          setHolding(false)
          return
        }

        const linkExists = json.links.some(edge => ( //check if links exist (change later in case of one way direction not two way, or parallel lines) fix?
          (edge.source === line.source && edge.target === id) ||
          (edge.source === id && edge.target === line.source)
        ));

        if (linkExists) { //link exist to return
          setline(null)
          setHolding(false)
          return
        }

        const copyline = { //do this so line end point is at the middle of circle
          type: 'line',
          source: line.source,
          target: id,
          color: color,
        }
        addLink(copyline)
        setline(null)
        setHolding(false)
      }
      else{ //not circle, erase line
        setline(null)
        setHolding(false)
      }
    }
  }

  function handleWheel(e){ //zoom function
    e.evt.preventDefault();

    const scaleBy = 1.02;

    var oldScale = stageRef.current.getStage().scaleX();
    var pointer = stageRef.current.getStage().getPointerPosition();

    var mousePointTo = {
      x: (pointer.x - stageRef.current.getStage().x()) / oldScale,
      y: (pointer.y - stageRef.current.getStage().y()) / oldScale,
    };

    // how to scale? Zoom in? Or zoom out?
    const direction = e.evt.deltaY > 0 ? 1 : -1;
    const newScale = direction > 0 ? oldScale * scaleBy : oldScale / scaleBy;

    stageRef.current.getStage().scale({ x: newScale, y: newScale });

    var newPos = {
      x: pointer.x - mousePointTo.x * newScale,
      y: pointer.y - mousePointTo.y * newScale,
    }
    stageRef.current.getStage().position(newPos);
  }

  /////

  const nodeElements = []
  const linkElements = []

  json.nodes.forEach((node) => {
    nodeElements.push(
    <Node key = {node.uID} nodeData = {node} mode = {mode} setselectedEle={setselectedEle}
      updateNode={updateNode} json = {json} showMenu ={showMenu} setmousePos = {setmousePos}/>
    )
  })

  json.links.forEach((link, index) =>{
    const foundSourceNode = json.nodes.find((node) => node.id === link.source)
    const foundTargetNode = json.nodes.find((node) => node.id === link.target)

    const sourceX = foundSourceNode.x
    const sourceY = foundSourceNode.y
    const targetX = foundTargetNode.x
    const targetY = foundTargetNode.y

    if (link.type === 'arrow'){
      linkElements.push( //keys change when index changes (array length is modified), which makes boxes flicker, maybe change it to a uid????
        <ArrowLinks key = {index} linkData = {link} points = {[sourceX, sourceY, targetX, targetY]} setmousePos={setmousePos} 
        updateLinks = {updateLinks} setselectedEle = {setselectedEle} showMenu={showMenu}/>
      )
    }
    else{
      linkElements.push( //keys change when index changes (array length is modified), which makes boxes flicker, maybe change it to a uid????
        <LineLinks key = {index} linkData = {link} points = {[sourceX, sourceY, targetX, targetY]} setmousePos={setmousePos} 
        updateLinks = {updateLinks} setselectedEle = {setselectedEle} showMenu={showMenu}/>
      )
    }
  })

  return (
    <div id = 'HomePage'>
      <p id = 'Warning'>Please do not make the colors white or else you won't see them here</p>
      <div id = 'menu' onClick={() => showMenu(false)}>
        <input type="file" accept="application/json" onChange={importFile}/>
        <p>Drag an image to make a node with that image</p>
        <p><strong>------------------------</strong></p>
        <p>Toggle Z to switch Modes</p>
        <p>Current Mode is <strong>{mode.charAt(0).toUpperCase() + mode.slice(1)}</strong></p>
        <p><strong>------------------------</strong></p>
        <p>Drag the stage to pan</p>
        <p>Use the mouse wheel to zoom in/out</p>
        <p><strong>------------------------</strong></p>
        <p>Double Click on the stage to make a label</p>
        <p>Double Click on an existing label to change the label</p>
        <p>RightClick on the node or line label to open menu for changing colors, switch arrows, or delete</p>
        <p><strong>------------------------</strong></p>
        {explainMode()}
        <button className = "buttonSide" onClick={() => convertToImage()}>Convert to Image</button>
        <button className = "buttonSide" style = {{left: 110}} onClick={() => convertToJSON()}>Convert to JSON</button>
      </div>
      <div id = 'canvasDiv' onClick={() => showMenu(false)}
        onDrop={e => handleDropImage(e)}
        onDragOver={e => e.preventDefault()}
      >
        <Stage ref = {stageRef} draggable = {lineholding ? false : true} onWheel={e => handleWheel(e)}
          onMouseDown={e => handleMouseDown(e)} onMouseMove={e => handleMouseMove(e)} onMouseUp={e => handleMouseUp(e)}
          id = 'canvas' width={window.innerWidth} height={window.innerHeight}>
          <Layer>
            {linkElements}
            {line !== null ? 
              <>
                {mode === 'arrow' ?
                <Arrow
                key = 'moveline'
                fill = {color}
                points = {[line.startPoint[0], line.startPoint[1], line.endPoint[0],line.endPoint[1]]}
                stroke = {color}
                strokeWidth = {2}
                /> : 
                <Line
                key = 'moveline'
                fill = {color}
                points = {[line.startPoint[0], line.startPoint[1], line.endPoint[0],line.endPoint[1]]}
                stroke = {color}
                strokeWidth = {2}
                /> 
                }
              </>
            :null}
          </Layer>
          <Layer>
            {nodeElements}
          </Layer>
          {/* <Layer> //this is for selected node
            {selectedEle ? 
              <Rect

              />
            :null}
          </Layer> */}
        </Stage>
      </div>
      {menuBool ? 
        <div id = 'RightClickMenu' style ={{left: mousePos.x, top: mousePos.y}} >
          <CompactPicker color = {selectedEle.color} 
          onChange={e => {
            setColor(e.hex)
            if ('id' in selectedEle) updateNode(selectedEle.id, 'color', e.hex) //is node
            else updateLinks('ColorUpdate', [selectedEle.source, selectedEle.target, e.hex])//is link
          }}/>
          <button className='buttonMenu' onClick={() => {
            if ('id' in selectedEle) updateNode(selectedEle.id, 'delete') //is node
            else updateLinks('DeleteUpdate', [selectedEle.source, selectedEle.target])//is link
            showMenu(false)
            setselectedEle(null)
          }}>Delete</button>
          <br/>
          {LinkMenuOptions()}
        </div>
      : null}
    </div>
  );
};

export default App
